body{
  overflow: hidden;
}

.desktop-container{
  display: block;
}

.mobile-container{
  display: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.headerMenuContainer{
  position: fixed;
  width: 100%;
  top:0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover:not(.active) {
  background-color: #111;
}

.active {
  background-color: #04AA6D;
}

.floatRight{
  float: right;
}

.fileUploadContainer{
  width: 50%;
  margin: 20% 25% 0;
}

.bannerImg{
  width: 100%;
  height: 100%;
  background-image: url("images/facts.jpg");
}

.employeeDashboardConatiner{
  margin-top:5%;
  width: 100%;
}

.sc-fFSPTT{
 text-align: left;
}

.rdt_TableCol{
  border: solid 1px black;
}

.rdt_TableCell{
  border-top: 0;
  border-bottom: solid 1px black;
  border-right: solid 1px black;
}


@media only screen and (max-width: 600px) {
  .bannerImg{
    width: 100%;
    height: 100%;
    background-image: url("images/fact-mob.jpg");
  }

  .desktop-container{
    display: none;
  }
  
  .mobile-container{
    display: block;
  }

  .mobile-container {
    max-width: 480px;
    margin: auto;
    background-color: #555;
    color: white;
    border-radius: 10px;
  }
  
  .topnav {
    overflow: hidden;
    background-color: #333;
    position: relative;
  }
  
  .topnav #myLinks {
    display: none;
  }
  
  .topnav a {
    color: white;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    display: block;
  }
  
  .topnav a.icon {
    background: black;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
  }
  
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .active {
    background-color: #04AA6D;
    color: white;
  }

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
